"use server";

import { Header } from "@/components/navigation/header";
import { Sidebar } from "@/components/navigation/sidebar";
import { TagBar } from "@/components/navigation/tag-bar";
import { ENDPOINTS } from "@/shared/site";
import { cookies } from "next/headers";

async function getData() {
  const res = await fetch("http://localhost:8000/api/v1/user/profile", {
    method: "GET",
  });

  if (!res.ok) {
    const data = await res.json();
    if (data.code === "auth_required" && res.status === 401) {
      const cookieStore = await cookies();

      const allCookies = cookieStore.getAll();
      const cookieHeader = allCookies
        .map((cookie) => `${cookie.name}=${cookie.value}`)
        .join("; ");
      const res = await fetch("http://localhost:3000/api/auth/refresh", {
        method: "POST",
        headers: { Cookie: cookieHeader },
      });
      console.log("Failed");
    }
    return;
  }
  return res.json();
}
export default async function User() {
  const data = await getData();
  // console.log(data);

  return (
    <main>
      <TagBar />
      <div className="flex">
        <Sidebar />
        <div className="flex-1 min-h-screen pt-6">
          <Header />
          <div className="h-screen p-4">{/* <CustomTable /> */}</div>
        </div>
      </div>
    </main>
  );
}
